*,
::after,
::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
div{
    display: block;
}
h1,
h2,
h3,
h4,
h5,
h6,
button {
    text-transform: capitalize;
}
html{
    scroll-behavior: smooth;
}
body {
    overflow-y: auto;
    overflow-x: hidden;
}

button {
    border: none;
    cursor: pointer;
    border-radius: 2px;
}

img {
    display: block;
    object-fit: cover;

}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
a{
    font-family: 'Montserrat', sans-serif;
}
a,select,button{

    &:focus{
        border: none;
        outline: 1.5px solid rgb(187, 229, 255);
    }
}
p {
    font-family: 'Poppins', sans-serif;
    ;
}